import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Categoria from './components/categorias/Index.vue';
import Rol from './components/roles/Index.vue';
import Usuario from './components/seguridad/Index.vue';
import Productos from './components/productos/Index.vue';
import Salidas from './components/salidas/Create.vue';

Vue.use(Router)
export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/categorias',
      name: 'categorias',
      component: Categoria
    },
    {
      path: '/prod',
      name: 'prod',
      component: Productos
    },
    {
      path: '/roles',
      name: 'roles',
      component: Rol
    },
    {
      path: '/usuarios',
      name: 'usuarios',
      component: Usuario
    },
    {
      path: '/salidas',
      name: 'salidas',
      component: Salidas
    }
  ]
})
