<template>
    <v-layout justify-center>
        <v-flex xs12>
            <v-toolbar color="white">
            <v-toolbar-title>SALIDA DE EQUIPOS</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn color="brown" dark>BUSCAR EQUIPOS<v-icon dark >search</v-icon> </v-btn>
            </v-toolbar>
            <v-card>
                 <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                           <v-card>
                                <v-card-title class="error" style="color: white;margin-top:3px"><h3>ITEM EQUIPOS</h3></v-card-title>
                                        <v-container grid-list-md>
                                            <v-layout wrap>
                                                <v-flex xs6>
                                                    <v-select :items="usuario" v-model="idusuario" prepend-icon="assignment" label="FUNCIONARIO"></v-select>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-text-field v-model="bcodBarras" type="number" prepend-icon="looks_one" label="COD. BARRAS"></v-text-field>
                                                </v-flex>
                                            </v-layout>
                                        </v-container>
                                        <v-card-text>
                                        <v-data-table :headers="cabeceraEquipos" :items="detalles" hide-actions  class="elevation-1 margen">
                                            <template  slot="items" slot-scope="">
                                                <td class="margen">
                                                    <v-icon color="red darken-2" small>delete</v-icon>
                                                </td>
                                            </template>
                                            <template slot="no-data">
                                                <span class="red--text">
                                                    <h3> No hay equipos agregados al detalle.</h3>
                                                </span>
                                            </template>
                                        </v-data-table>
                                        <v-flex class="text-xs-right">                                  
                                        </v-flex>
                                    </v-card-text>
                                <v-divider></v-divider>
                           </v-card>
                        </v-flex>
                    </v-layout>
                 </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            idusuario:'',
            bcodBarras:'',
            detalles:[],
            usuario:[],
            cabeceraEquipos: [
                    { text: 'OP', value: 'opciones',sortable: false},
                    { text: 'FECHA INGRESO', value: '',sortable: false},
                    { text: 'SERIE', value: '',sortable: false},
                    { text: 'DIGITO', value: '',sortable: false},
                    { text: 'MODELO', value: '',sortable: false},
                    { text: 'EJECUTIVO', value: '',sortable: false},
                    { text: 'OBSERVACION', value: '', sortable: false  },       
            ],
        }
    },
    created() {
        this.selectUsuario();  
    },
    methods: {
        selectUsuario(){
            let me=this;
            var uArray=[];
            axios.get('usuario/listar').then(function(response){
                uArray=response.data;
                uArray.map(function(x){
                    me.usuario.push({text: x.name,value:x.idusuario});
                });
            }).catch(function(error){
            });
        },
    },




}
</script>


<style>
    .tabs{
         padding: 0%;
    }
</style>